/* Font properties: https://www.cdnfonts.com/akrobat.font */
@import url('https://fonts.cdnfonts.com/css/poppins');

//@import "slick-carousel/slick/slick.css";
//@import "slick-carousel/slick/slick-theme.css";

$body-color: #F1F2EC;
$color-brand-green: #B2C0B1;
$color-brand-darkgreen: #052819;
$color-text-gray: #6E6E6E;

$mobile-w0: 414px;
$mobile-w10: 990px;
$mobile-w20: 1024px;

$mobile-h5: 820px;

$padding1: 16px;
$padding2: 32px;

* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

html, body {
  margin: 0;
}
body {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  min-width: 340px;
  min-height: 667px;
  font-size: 18px;
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
}

.dev {
  outline: 1px dashed red;
  outline-offset: -1px;
}

.p1 {
  padding: $padding2;
}

.pv2 {
  padding-top: $padding2;
  padding-bottom: $padding2;
}
.mr2 {
  margin-right: $padding2;
}

h2  {
  font-style: normal;
  font-weight: 900;
  font-size: 45px;
  line-height: 68px;
  /* identical to box height */
  letter-spacing: 0.07em;
  text-transform: uppercase;
  text-align: center;
}

.badge {
  background-color: #fff;
  color: black;
  display: inline-block;
  border-radius: 5px;
  line-height: 1.4;
  padding-left: 8px;
  padding-right: 8px;
}

.container {
  margin-left: 40px;
  margin-right: auto;
  min-width: $mobile-w0;
  max-width: 1440px;
}

.menu {
  height: 122px;
  justify-content: flex-start;
  padding-left: 40px;
  background: $color-brand-green;
  color: white;
  display: flex;
  flex-direction: row;
  align-items: center;

  > * {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: $padding1;
  }

  &-logo {
    margin-right: 100px;
    svg {
      height: 60px;
      width: auto;
      color: $color-brand-darkgreen;
    }
  }
  &-item {
    color: white;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 15px;
    font-weight: 600;
    letter-spacing: 1.2px;
    height: 122px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 4px;
    padding-right: 4px;

    &:hover {
      background-color: rgba(0,0,0,.1);
    }

    &.active {
      background-color: black;
    }

    &s {
      display: flex;
      gap: $padding1;

    }
  }

  &.mode2 {
    background-color: transparent;
    .menu {
      &-logo {
        svg {
          color: white;
        }
      }

      &-item {
        background-color: transparent;
        text-decoration: underline;
        text-transform: none;
        font-weight: 400;
        font-size: 18px;
        letter-spacing: inherit;
      }
    }
  }


  @media (max-width: $mobile-w0) {
    flex-direction: column;
    border-bottom: none;
    flex-wrap: wrap;
    height: auto;
    padding-left: 0;
    &-logo {
      margin-right: 0;
      text-align: center;
      margin-bottom: 20px;
      margin-top: 20px;
    }
    &-item {
      height: 60px;
      &s {
        flex-wrap: wrap;
      }
    }
  }

  @media (max-width: $mobile-w10) {
    &-logo {
      margin-right: 40px;
      svg {
        height: 30px;
      }
    }
  }
}

footer {
  padding-top: 50px;
  background-color: $color-brand-darkgreen;
  color: white;
  line-height: 2.4;
  .badge {
    font-size: 20px;
  }
  a {
    color: white;
  }
  .menu {
    border-bottom: none;
    .container {
      margin-left: $padding2;
    }
  }
}

.screen {
  min-height: 100vh;
  overflow: hidden;
  &-first {
    min-height: calc(100vh - 122px);
  }
  &.green {
    background-color: $color-brand-green;
    color: white;
    a {
      color: white;
    }
  }
  &-p {
    padding: $padding2;
  }
  &-text-1 {
    font-weight: 900;
    text-transform: uppercase;
    font-size: 60px;
    max-width: 650px;
    margin-top: 90px;
    float: left;
  }
  &-img-1 {
    margin-top: 100px;
    margin-left: 100px;
    float: right;
  }
  @media (max-width: $mobile-w0) {
    &-text-1 {
      font-size: 35px;
    }
    &-img-1 {
      margin-top: 10px;
      margin-left: 10px;
      width: 100%;
    }
  }

  @media (max-height: $mobile-h5) {
    &-text-1 {
      font-size: 40px;
    }
    &-img-1 {
      margin-top: -40px
    }
  }

  @media (min-height: 990px) and (max-width: 1100px) {
    &-first {
      min-height: 800px;
    }
    &-img-1 {
      margin-top: -40px
    }
  }
}

.card {
  &-group {
    margin: 40px 15px;
    display: flex;
    justify-content: center;
    &.mode2 {
      gap: 16px;
    }
  }

  position: relative;
  box-shadow: 1px 4px 5px #bbbbbb;
  padding: $padding2;
  width: 428px;
  min-height: 320px;
  background: #FFFFFF;

  &-title {
    font-style: normal;
    font-weight: 900;
    font-size: 25px;
    line-height: 38px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
  }
  &-body {
    margin-top: 16px;
    position: relative;
    z-index: 1;
  }
  &-bg-img {
    position: absolute;
    right: 15px;
    bottom: 15px;
    z-index: 0;
  }
  &-btn {
    background-color: $color-brand-darkgreen;
    color: white;
    padding: 16px 28px;
    display: inline-block;
    position: absolute;
    border-radius: 12px;
    right: 15px;
    bottom: 15px;
    text-decoration: none;
  }

  &.mode2 {
    background-color: $color-brand-darkgreen;
    color: white;
  }

  &.mode3 {
    border: 1px solid black;
    border-bottom-width: 6px;
    box-shadow: none;
    .card {
      &-title {
        border-bottom: 1px solid black;
        margin-bottom: 26px;
        padding-bottom: 26px;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 26px;
      }
    }
  }

  @media (max-width: $mobile-w0) {
    width: auto;
    &-group {
      flex-direction: column;
      margin: 10px;
    }
  }
  @media (max-width: $mobile-w10) {
    &-group {
      margin: 10px;
    }
  }
}

.block2 {
  display: flex;
  flex-direction: row;
  margin: 100px 16px;
  box-shadow: 1px 4px 5px #bbbbbb;

  &-half {
    width: 50%;
    + .block2-half {
      padding-left: 16px;
    }
    img {
      max-width: 100%;
    }
  }
  &-title {
    font-style: normal;
    font-weight: 900;
    font-size: 45px;
    line-height: 68px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    color: $color-brand-darkgreen;
  }
  &-body {
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 26px;
    color: $color-text-gray;
  }
  &-lists {
    display: flex;
    ul {
      list-style: url("../img/li.svg");
      li {
        font-style: normal;
        font-weight: 400;
        font-size: 17px;
        line-height: 38px;
        color: $color-text-gray;
        position: relative;
        &::marker {
          display: inline-block;
          vertical-align: bottom;
          position:relative;
          bottom:11px;
          list-style-position: outside;
        }
      }
    }
  }

  @media (max-width: $mobile-w0) {
    flex-direction: column;
    display: block;
    &-half {
      width: auto;
      + .block2-half {
        border-left-width: 0;
      }
    }
    &-title {
      font-size: 32px;
      line-height: 1.4;
      margin-bottom: 20px;
    }
    &-body {
      margin-bottom: 30px;
    }
    &-lists {
      flex-direction: column;
    }
  }
}

.block3 {
  margin: 16px;
  display: flex;
  &-half {
    width: calc(100% - 708px);
    padding: 100px 180px 100px 50px;
    background-color: $color-brand-green;
    + .block3-half {
      background-color: white;
    }
  }
  &-title {
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 38px;
  }
  &-body {
    margin-top: 30px;
    margin-bottom: 30px;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    /* identical to box height */
  }
  &-btn {
    background-color: $color-brand-darkgreen;
    color: white;
    padding: 16px 28px;
    display: inline-block;
    border-radius: 12px;
    right: 15px;
    bottom: 15px;
    text-decoration: none;
  }
  &-bg-img {
    background-image: url(../img/block3.png);
    width: 708px;
    height: 435px;
  }
  @media (max-width: $mobile-w0) {
    flex-direction: column;
    display: block;
    &-half {
      width: 100% !important;
      padding: 20px;
    }
    &-bg-img {
      background-image: url(../img/block3.png);
      width: 100%;
      height: auto;
      aspect-ratio: 708/435;
      background-size: cover;
    }
  }
  @media (max-width: $mobile-w20) {
    &-half {
      width: 50%;
      padding: 40px;
    }
  }
}
.brand {
  &s {
    margin-top: 100px;
    margin-bottom: 100px;
    display: flex;
    gap: 16px;
    justify-content: center;
    @media (max-width: $mobile-w0) {
      flex-direction: column;
      align-items: center;
    }
  }
  &-item {
    img {
      max-height: 166px;
      max-width: 100%;
    }
  }

}

.hr-imgs {
  text-align: center;
  margin-top: 100px;
  margin-bottom: 100px;
  img {
    display: inline-block;
  }
}

.form {
  display: flex;
  flex-direction: row;
  background-color: #fff;
  margin: 50px 100px 50px;
  color: black;
  a {
    color: black !important;
  }
  .contacts {
    line-height: 2.4;
    .badge {
      background-color: #F3F3F3;
      font-size: 20px;
    }
  }
  &-half {
    width: 50%;
    padding: 80px;
    form {
      display: flex;
      flex-direction: column;
      gap: 20px;
    }
  }
  &-checkbox {
    &es {
      display: flex;
      flex-direction: row;
      gap: 32px;
    }
    input {
      height: 28px;
      width: 28px;
      display: none;
      &:checked {
        + label::after {
          background-color: #000;
        }
      }
    }
    label {
      cursor: pointer;
      position: relative;
      padding-left: 40px;
      &::before {
        cursor: pointer;
        content: '';
        display: inline-block;
        width: 26px;
        height: 26px;
        border: 1px solid black;
        border-radius: 50%;
        position: absolute;
        left: 0;
        top: 0;
        background-color: #fff;
      }
      &::after {
        cursor: pointer;
        content: '';
        display: inline-block;
        background-color: #F3F3F3;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        position: absolute;
        left: 6px;
        top: 6px;
      }
    }
  }
  &-input {
    label {
      margin-bottom: 4px;
      display: inline-block;
    }
    input {
      width: 100%;
      border: 1px solid black;
      border-radius: 12px;
      height: 60px;
      font-size: 18px;
      text-indent: 20px;
    }
  }
  &-textarea {
    input {}
    textarea {
      width: 100%;
      min-height: 190px;
      border: 1px solid black;
      border-radius: 12px;
      font-size: 18px;
      padding: 20px;
    }
  }
  &-button {
    /* Auto layout */
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 20px 35px;
    background: $color-brand-green;
    border-radius: 14px;
    border: none;
    cursor: pointer;
    font-size: 18px;
  }

  @media (max-width: $mobile-w0) {
    flex-direction: column;
    align-items: center;
    margin: 10px;
    &-half {
      width: auto;
      padding: 10px;
    }
  }
  @media (max-width: $mobile-w10) {
    flex-direction: column;
    &-half {
      width: auto;
      padding: 20px;
    }
  }
  @media (max-width: $mobile-w20) {
    &-half {
      padding: 40px;
    }
  }
}

.pre-form {
  display: flex;
  flex-direction: row;
  &-text {
    width: 50%;
    padding: 40px;
    color: $color-brand-darkgreen;
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 48px;
  }
  &-img {
    width: 50%;
    img {
      width: 100%;
    }
  }
  @media (max-width: $mobile-w0) {
    &-text {
      width: auto;
      padding: 20px;
      font-size: 24px;
      line-height: 1.3;
    }
    &-img {
      display: none;
      width: auto;
    }
  }
}